@font-face {
  font-family: "Ubuntu Mono";
  src: url("/fonts/ubuntu-mono/ubuntu-mono-regular.ttf");
}
@font-face {
  font-family: "Open Sans";
  src: url("/fonts/open-sans/open-sans-light.ttf");
}
@font-face {
  font-family: Slabo;
  src: url("/fonts/slabo-27px/slabo-27px-regular.ttf");
}
body {
  margin: 0;
}
/*

All sections

*/
#menu-outer *,
#content-outer *,
#bottom-outer * {
  font-family: "Slabo", serif;
  line-height: 1.5rem;
}
#menu-outer a,
#content-outer a,
#bottom-outer a {
  color: #32373c;
}
/*

Top menu

*/
#menu-outer {
  padding: 0.6rem 1rem;
  background-color: #f0f5fa;
  border-bottom: 1px solid #c8d2dc;
}
#menu-outer * {
  font-family: "Open Sans", sans-serif;
}
#menu-outer #menu-inner a,
#menu-outer #menu-inner div {
  display: inline-block;
  margin-right: 0.6rem;
}
/*

On the index page to the bottom

*/
#about-outer {
  padding: 0.6rem 1rem;
  display: flex;
}
#about-outer #about-left {
  padding: 0.6rem 1rem;
  font-family: "Slabo", serif;
  flex: 0.6;
}
#about-outer #about-right {
  flex: 0.4;
}
#about-outer #about-right img {
  width: 200px;
}
#content-inner {
  padding: 0.6rem 1rem;
  display: flex;
  margin: auto;
}
#content-inner #index-left {
  padding: 0.6rem 1rem;
  flex: 0.7;
}
#content-inner #index-right {
  padding: 0.6rem 1rem;
  flex: 0.3;
}
#content-inner .page,
#content-inner .post {
  margin: auto;
  width: 960px;
}
#content-inner h1,
#content-inner h2,
#content-inner h3,
#content-inner h4,
#content-inner h5,
#content-inner h6 {
  font-family: "Open Sans", sans-serif;
  color: #1e2328;
}
#content-inner h1>*,
#content-inner h2>*,
#content-inner h3>*,
#content-inner h4>*,
#content-inner h5>*,
#content-inner h6>* {
  font-family: "Open Sans", sans-serif;
}
#content-inner code,
#content-inner pre,
#content-inner .code-editor,
#content-inner .ace-editor {
  font-family: "Ubuntu Mono", Inconsolata, Menlo, Consolas, monospace;
  background-color: #f0f5fa;
  line-height: 1.5rem;
}
#content-inner code *,
#content-inner pre *,
#content-inner .code-editor *,
#content-inner .ace-editor * {
  font-family: "Ubuntu Mono", Inconsolata, Menlo, Consolas, monospace;
}
#content-inner blockquote {
  padding: 0.6rem 1rem;
  background-color: #f0f5fa;
}
#content-inner .examples {
  display: flex;
  flex-wrap: wrap;
}
#content-inner .example {
  padding: 0.6rem 1rem;
  background-color: #f0f5fa;
  border: 1px solid #c8d2dc;
  flex: 0.3;
  flex-direction: column;
  margin-right: 0.6rem;
  text-align: center;
}
#content-inner .example a {
  flex: 0.7;
}
#content-inner .example .caption {
  flex: 0.3;
}
#content-inner .example video,
#content-inner .example img {
  max-width: 200px;
}
#content-inner hr {
  border-color: #c8d2dc;
  border-style: solid;
  border-width: 0 0 1px 0;
}
#content-inner table {
  border-collapse: collapse;
}
#content-inner table th,
#content-inner table td {
  border-bottom: 1px solid #c8d2dc;
  padding: 0.3rem;
}
#bottom-outer {
  font-family: "Open Sans", sans-serif;
  padding: 0.6rem 1rem;
  border-top: 1px solid #c8d2dc;
  margin-top: 3rem;
}
#bottom-outer a {
  margin-right: 0.6rem;
}
#github-repos .github-repo-item,
#github-repo-hexo .github-repo-item {
  padding: 0.6rem 1rem;
  display: flex;
  flex-direction: row;
  margin-bottom: 0.6rem;
}
#github-repos .github-repo-item .repo-title,
#github-repo-hexo .github-repo-item .repo-title {
  display: flex;
  flex: 0.7;
  flex-direction: column;
}
#github-repos .github-repo-item .repo-title .name,
#github-repo-hexo .github-repo-item .repo-title .name {
  flex: 0.5;
}
#github-repos .github-repo-item .repo-title .description,
#github-repo-hexo .github-repo-item .repo-title .description {
  flex: 0.5;
}
#github-repos .github-repo-item .tech-metrics,
#github-repo-hexo .github-repo-item .tech-metrics {
  display: flex;
  margin-left: auto;
  text-align: right;
}
#github-repos .github-repo-item .tech-metric,
#github-repo-hexo .github-repo-item .tech-metric {
  align-items: center;
  background-color: #f0f5fa;
  border-radius: 0.6rem;
  display: flex;
  font-size: 0.85rem;
  height: 1.2rem;
  margin-right: 0.6rem;
  padding: 0.2rem 0.3rem;
}
#github-repos .github-repo-item .tech-metric .tech-text,
#github-repo-hexo .github-repo-item .tech-metric .tech-text {
  font-family: "Ubuntu Mono", Inconsolata, Menlo, Consolas, monospace;
  color: $color-fg-highlight;
  flex: 0.5;
}
#github-repos .github-repo-item .tech-metric .tech-bg,
#github-repo-hexo .github-repo-item .tech-metric .tech-bg {
  flex: 0.5;
}
#github-gists .github-gist-item {
  padding: 0.6rem 1rem;
  display: flex;
  flex-direction: row;
}
#github-gists .github-gist-item .gist-description {
  flex: 0.7;
}
#github-gists .github-gist-item .gist-date {
  margin-left: auto;
}
#recent-posts .recent-post-item {
  padding: 0.6rem 1rem;
  display: flex;
  flex-direction: row;
}
#recent-posts .recent-post-item .recent-post-title {
  flex: 0.7;
}
#recent-posts .recent-post-item .recent-post-date {
  margin-left: auto;
}
#contributions {
  display: flex;
  flex-direction: column;
}
#contributions .contributions-item {
  border-bottom: 1px solid #c8d2dc;
}
#contributions .contributions-item .title,
#contributions .contributions-item img {
  padding: 0.6rem 1rem;
}
#contributions .contributions-item img {
  max-width: 400px;
}
#contributions .contributions-item ul {
  font-size: 0.9rem;
}
